.container {
    width: 280px;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
}

@media screen and (min-width: 768px) {
    .container {
    width: 704px;
    padding-left: 32px;
    padding-right: 32px;
    }
}

@media screen and (min-width: 1280px) {
    .container {
    width: 1248px;
    padding-left: 16px;
    padding-right: 16px;
    }
}